.App {
  overflow: hidden;
}

.video-wrapper {
  width: 150vw;
  height: 100vh;
}

.video {
  height: 150%;
  z-index: -1;
}

.menu-wrapper {
  position: absolute;
  z-index: 1;
  left: 10vw;
  top: 15vh;
  color: #FFF;
}

.home-link {
  text-decoration: none;
  color: #FFF;
}

.link-dot:hover:after {
  content: "";
  position: absolute;
  background: #FFF;
  width: 8px;
  height: 8px;
  border-radius: 100%;
}

.link-dot:hover {
  cursor: pointer;
}

.text {
  width: 30vw;
}